<template>
  <div class="card" :style="cardGlobalStyle()">
    <div class="card-body">
      <h5 class="card-title">{{ title }}</h5>
      <p class="card-text">{{ description }}</p>  
      <slot></slot>
    </div>
  </div>
</template>


<style scoped lang="scss">
.card {
  width: 100%;
  border:0px;
  border-radius: 10px;
  // -webkit-box-shadow: 3px 13px 26px -16px #000000; 
  // box-shadow: 3px 13px 26px -16px #000000;
-webkit-box-shadow: 0px 19px 35px -5px rgba(0,0,0,0.72); 
box-shadow: 0px 19px 35px -5px rgba(0,0,0,0.72);
}
</style>

<script>
import { mapState } from "vuex";

export default {
  name: "card",
  props: ["title", "description", "text"],
  data: function () {
    return {};
  },
  methods: {
    cardGlobalStyle() {
      let contentBg = 'background-color: ' + this.style.currentMode.contentBg + '; ';
      let color = 'color: ' + this.style.currentMode.color + '; ';
      return contentBg + color;
    },
  },
  computed: {
    ...mapState({
      style: (store) => store.style,
    }),
  },
};
</script>